import {WinJSDec, fnError} from './APP/config';

var name = 'CUSTOMER_EVENT_TEXT';
var CUSTOMER_EVENT_TEXT = (fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      document.addEventListener('CHARTBAR_EVENT_TEXT_SEND', fn);
    })
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default CUSTOMER_EVENT_TEXT;
